.c-card {
  background-color: #FFFFFF;
  border-radius: $global-radius;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: $global-module-size; 
  position: relative; 

  &:not(last-child) {
    margin-bottom: $global-module-size;
  }

  @include breakpoint(large-up) {
    flex-direction: row;
    height: rem(250);
    justify-content: space-between;
  }

  &.is-closed {
    pointer-events: none;

    &::before {
      width: 100%;
      height: 100%;
      color: #FFF;
      font-size: 250%;
      content: "Fechado";
      position: absolute;
      background-color: rgba(grey, 0.95);
      left: 0;
      top: 0;
      z-index: 1;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  
  &__image {
    align-self: center;
    border-radius: 50%;
    max-width: rem(160);
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__identification {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include breakpoint(large-up) {
      text-align: inherit;
      align-items: flex-start;
    }
  }

  .c_tags {
    margin: 0;
  }

  &__name {
    margin-bottom: 0;
    line-height: 1;

    @include breakpoint(large-up) {
      margin-top: 0;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    margin-top: $global-module-size;

    @include breakpoint(small-up) {
      flex-direction: row;
      justify-content: center;
    }

    @include breakpoint(large-up) {
      justify-content: flex-start;
      margin-top: 0;
    }
  }

  .c-iconized-info {
    width: 100%;

    @include breakpoint(small-up) {
      width: inherit;
    }

    &:not(:last-child) {
      margin-bottom: $global-module-size / 2;

      @include breakpoint(small-up) {
        margin-bottom: inherit;
        margin-right: $global-module-size;
      }
    }
  }

  &__cta {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: $global-module-size;

    @include breakpoint(large-up) {
      margin-top: $global-module-size;
      padding-top: rem(10);
    }

    .c-button {
      align-self: center;
      padding: rem(15) rem(20);

      @include breakpoint(large-up) {
        align-self: start;
        margin-bottom: $global-module-size / 2;
        padding: rem(5) rem(10);
      }
    }

    .c-iconized-info {
      width: inherit;
    }
  }

  &__favorite {
    fill: var(--color-orange);
    position: absolute;
    right: $global-module-size;
    top: $global-module-size;
    width: rem(20);
    height: rem(20);

    &:hover {
      transform: scale(1.2);
    }
  }
}