.l-header {
  background-color: var(--color-orange);
  grid-area: header;
  height: rem(70);
  
  grid-template-columns: $global-max-width;
  justify-content: center;

  @include breakpoint(large-up) {
    display: grid;
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    align-items: center;
  }

  &__link {
    text-decoration: none;
    color: #FFF;
    font-weight: bold;
    font-size: rem(30);
    display: flex;
    align-items: center;
  }

  &__img {
    fill: #FFF;
    width: 50px;
    height: 50px;
  }

  &__tags {
    display: flex;
    padding: 0;
    margin: 0;
  }

  &__items {
    list-style-type: none;

    &:not(:last-child) {
      margin-right: $global-module-size;
    }
  }

  &__redirect {
    text-decoration: none;
    color: #FFF;
  }

  @media (max-width: 1250px) {        
    
    &__content {
      flex-direction: column;      
    }
  }
}