.l-footer {
  background-color: var(--color-green);
  grid-area: footer;

  display: flex;
  flex-direction: column;

  &__info {
    display: flex;
    align-items: center;

    @include breakpoint(large-up) {
      height: rem(200);
    }
  }

  &__copyright {
    background-color: var(--color-green-dark);
    display: flex;
    align-items: center;
    color: #68C166;

    @include breakpoint(large-up) {
      height: rem(75);
    }
  }

  &__content {    
    display: flex;
    justify-content: space-between;
    
    @include breakpoint(large-up) {
      max-width: $global-max-width;
      margin: 0 auto;
    }    
  }

  &__logo {
    display: flex;
    align-items: center;
    margin-right: $global-module-size;
  }

  &__about {
    margin-right: $global-module-size;
  }

  &__social {
    margin-right: $global-module-size;
  }

  &__name {
    color: #68C166;
  }

  &__img {
    width: rem(70);
    height: rem(70);
    fill: #68C166;
  }

  &__tags {
    list-style-type: none;
    padding: 0;
  }

  &__link {
    text-decoration: none;
    color: #68C166;

    &:hover {
      color: var(--color-green-dark);
    }
  }

  &__social {
    width: 250px;
  }

  &__title {
    color: #68C166;
    font-size: rem(30);
    display: flex;
    justify-content: center;
    align-items: center;    
  }

  &__social-tags {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
  }

  &__social-link {
    fill: #68C166;
    transition: 0.3s ease-in-out;

    &:hover {
      fill: var(--color-green-dark);
    }
  }

  &__social-icon {
    height: 50px;
    width: 50px;
  }

  &__apps {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}