.c-filter {
  display: flex;
  flex-direction: column;
  border: 0;
  padding: 0;

  &:not(:last-child) {
    margin-bottom: $global-module-size;
  }
  
  &__legend {
    border-bottom: rem(1) solid var(--color-grey);
    display: inline-block;
    margin-bottom: $global-module-size / 2;
    padding-bottom: rem(5);
    width: 100%;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: rem(10);
    }    
  }
}