.c-tags {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;

  &__item {
    margin: rem(10) rem(5);

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    background-color: var(--color-grey);
    text-decoration: none;
    border-radius: $global-radius;
    font-size: rem(14);
    padding: rem(5) rem(10);
    color: #FFF;

    &:hover {
      background-color: var(--color-orange);
    }
  }
}