.l-aside {
  background-color: #FFF;
  grid-area: aside;
  padding: $global-module-size;
  border-radius: $global-radius;
  margin-bottom: $global-module-size;

  max-height: rem(200);
  overflow: auto;

  @include breakpoint(large-up) {
    margin-bottom: inherit;
  }

  @include breakpoint(large-up) {
    max-height: none;
  }
}